.container {
  display: flex;
}

.p-nohayresultado {
  color: #fff;
}

@media (max-width: 767px) {
  .container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-top: 20px;
  }
}
@media (min-width: 768px) and (max-width: 1023px) {
  .container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}
@media (min-width: 1024px) {
  .container {
    justify-content: space-between;
    flex-direction: row-reverse;

    padding-left: 100px;
    padding-right: 40px;
  }
}
